.insert-video-input {
  display: flex;
}
.insert-video-input .insert-video-input-label {
  width: 80px;
}
.insert-video-tip {
  font-size: 12px;
}
.insert-video-tip a {
  color: #38f;
}
.insert-video-preview {
  min-height: 280px;
  margin-top: 10px;
  background-color: #ddd;
  text-align: center;
  font-size: 20px;
  position: relative;
}
.insert-video-preview .insert-video-preview-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.insert-video-iframe {
  width: 100%;
  margin-top: 10px;
  min-height: 280px;
}
.oa-search-box {
  font-size: 0;
  display: flex;
  align-items: center;
}
.oa-search-box .el-input__inner {
  border-radius: 4px 0 0 4px;
}
.oa-search-box .oa-search-button {
  border-radius: 0;
  padding: 0 15px;
  height: 30px;
  box-sizing: border-box;
  line-height: 30px;
}
.supplier-list {
  background: white;
  border-radius: 4px;
}
