.goods-list-filter .title-box {
  font-size: 14px;
  font-family: MicrosoftYaHei;
  font-weight: 400;
  margin-right: 20px;
}
.goods-list-filter .title-box .list-title {
  line-height: 19px;
  margin-right: 10px;
  color: #20252c;
  font-weight: 600;
}
.goods-list-filter .tab-type .list-title {
  cursor: pointer;
  color: #333333;
  font-weight: 400;
}
.goods-list-filter .tab-type .activity-tab {
  color: #20252c;
  font-weight: 600;
  padding-bottom: 5px;
  border-bottom: 2px solid #409eff;
}
