.materials-video-player {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 800;
  margin: 0;
  background-color: rgba(0, 0, 0, 0.5);
}
.materials-video-player .video-player-popover {
  position: absolute;
  width: 100vw;
  height: 100vh;
}
.materials-video-player .video-player-main {
  position: relative;
  width: 800px;
  margin: 10% auto 0;
}
.materials-video-player .video-player-closer {
  position: absolute;
  right: -15px;
  top: -15px;
  z-index: 900;
  color: #e4e7ed;
  font-size: 30px;
  cursor: pointer;
}
