.newstyle div {
  outline: none;
}
.newstyle .edui-editor-body {
  min-height: 500px;
}

.newstyle .edui-btn-toolbar .edui-btn .edui-icon.edui-icon-minapp {
  width: 64px !important;
  background-size: 24%;
  background-position: 4px center;
  background-repeat: no-repeat;
  background-image: url(data:image/png;base64,);
}
.newstyle .edui-btn-toolbar .edui-btn .edui-icon.edui-icon-minapp::after {
  content: '\5c0f\7a0b\5e8f';
  margin-left: 22px;
  font-size: 12px;
  line-height: 40px;
  vertical-align: bottom;
}

.newstyle .edui-btn-toolbar .edui-btn .edui-icon.edui-icon-virtualreality {
  width: 20px !important;
  background-size: 95%;
  background-position: 0px center;
  background-repeat: no-repeat;
  background-image: url(data:image/png;base64,);
}

.newstyle.error .edui-container {
  border: 1px solid #f56c6c;
}
.newstyle .edui-btn-toolbar .edui-btn .edui-icon {
  height: 40px !important;
  width: 46px !important;
  background-image: url(data:image/png;base64,);
  background-image: url(/resources/dss-web-portal/content/image/icons2.13414a0e.gif);
}

.newstyle .edui-toolbar .edui-btn-toolbar {
  text-align: left;
}

.newstyle .edui-btn-toolbar .edui-btn.edui-active,
.newstyle .edui-btn-toolbar .edui-btn.edui-hover {
  background-color: #eee;
  border: 1px solid #eee;
  border-radius: 5px;
}

.newstyle .edui-btn-toolbar .edui-btn.edui-active.edui-combobox,
.newstyle .edui-btn-toolbar .edui-btn.edui-hover.edui-combobox {
  border: none;
}

.newstyle .edui-toolbar .edui-separator {
  background: #c9c4c4;
  width: 1px;
  padding: 0;
  height: 30px;
  margin: 5px;
  box-sizing: border-box;
}

.newstyle .edui-btn-toolbar .edui-btn .edui-icon-fullscreen {
  background-position: 0 -50px;
}

.newstyle .edui-btn.edui-btn-fullscreen.edui-active .edui-icon-fullscreen {
  background-position: -50px -50px;
}

.newstyle .edui-btn-toolbar .edui-btn .edui-icon-source {
  background-position: -100px -50px;
}

.newstyle .edui-btn-toolbar .edui-btn .edui-icon-undo {
  background-position: 0 0;
}

.newstyle .edui-btn-toolbar .edui-btn .edui-icon-redo {
  background-position: -50px 0;
}

.newstyle .edui-btn-toolbar .edui-btn .edui-icon-bold {
  background-position: -100px 0;
}

.newstyle .edui-btn-toolbar .edui-btn .edui-icon-italic {
  background-position: -150px 0;
}

.newstyle .edui-btn-toolbar .edui-btn .edui-icon-underline {
  background-position: -200px 0;
}

.newstyle .edui-btn-toolbar .edui-btn .edui-icon-strikethrough {
  background-position: -400px -50px;
}

.newstyle .edui-btn-toolbar .edui-btn .edui-icon-superscript {
  background-position: -750px 0;
}

.newstyle .edui-btn-toolbar .edui-btn .edui-icon-subscript {
  background-position: -800px 0;
}

.newstyle .edui-btn-toolbar .edui-btn .edui-icon-font,
.newstyle .edui-btn-toolbar .edui-btn .edui-icon-forecolor {
  background-position: -202px -50px;
}

.newstyle .edui-btn-toolbar .edui-btn {
  height: 40px;
}

.newstyle .edui-splitbutton-color-label {
  left: 0;
  margin: auto;
  right: 0;
  bottom: 5px;
}

.newstyle .edui-splitbutton .edui-caret {
  position: relative;
  display: inline-block;
  vertical-align: top;
  width: 5px;
  height: 100%;
  background: url(data:image/png;base64,) center no-repeat;
  background-size: 100% auto;
}

.newstyle .edui-btn-toolbar .edui-btn .edui-icon-backcolor {
  background: url(data:image/png;base64,) center no-repeat;
  background-size: auto 20px;
}

.newstyle .edui-btn-toolbar .edui-btn .edui-icon-removeformat {
  background-position: -350px -50px;
}

.newstyle .edui-btn-toolbar .edui-btn .edui-icon-insertorderedlist {
  background-position: -250px 0;
}

.newstyle .edui-btn-toolbar .edui-btn .edui-icon-insertunorderedlist {
  background-position: -300px 0;
}
.newstyle .edui-btn-toolbar .edui-btn-name-link {
