  background-position: right -106px;
  box-sizing: border-box;
  /* width: 100px; */
  font-size: 12px;
  height: 40px;
  line-height: 40px;
  padding-left: 15px;
  padding-right: 10px;
  white-space: nowrap;
  margin: 0 3px 0 0;
  text-align: left;
}

.newstyle .edui-combobox .edui-caret {
  position: relative;
  display: inline-block;
  vertical-align: top;
  width: 5px;
  height: 100%;
  background: url(data:image/png;base64,) center no-repeat;
  background-size: 100% auto;
}

