.newstyle .edui-btn-toolbar .edui-btn .edui-icon-drafts {
  background: url(data:image/png;base64,) center no-repeat;
  background-size: auto 20px;
}

.newstyle .edui-btn-toolbar .edui-btn .edui-icon-formula {
  background: url(data:image/png;base64,) center no-repeat;
  background-size: auto 20px;
