.newstyle .edui-btn-toolbar .edui-btn .edui-icon-emotion {
  background: url(data:image/png;base64,) center no-repeat;
  background-size: auto 20px;
}

.newstyle .edui-btn-toolbar .edui-btn .edui-icon-map {
  background: url(data:image/png;base64,) center no-repeat;
  background-size: auto 20px;
}

.newstyle .edui-btn-toolbar .edui-btn .edui-icon-print {
  background: url(data:image/png;base64,) center no-repeat;
  background-size: auto 20px;
}

.newstyle .edui-btn-toolbar .edui-btn .edui-icon-preview {
  background: url(data:image/png;base64,) center no-repeat;
  background-size: auto 20px;
}
.newstyle .edui-editor-body ul,
.newstyle .edui-editor-body ol {
  width: 95%;
  padding-left: 2.2em;
}
.newstyle .edui-editor-body ul,
.newstyle .edui-editor-body ul li {
  list-style-type: disc;
}
.newstyle .edui-editor-body ol,
.newstyle .edui-editor-body ol li {
  list-style-type: decimal;
}
.audio-box {
  width: 100% !important;
  height: 82px;
}
